const routers = [{
        path: '/main',
        name: "main",
        component: () =>
            import ( /* webpackChunkName: "main" */ '@/page/home/main/index'),
        redirect: '/main/role',
        children: [{
                path: '/main/role',
                name: 'role',
                component: () =>
                    import ( /* webpackChunkName: "main" */ '@/page/home/role/index'),
            },
            {
                path: '/main/sprinte',
                name: 'sprinte',
                component: () =>
                    import ( /* webpackChunkName: "main" */ '@/page/home/sprinte/index'),
            },
            {
                path: '/main/bags', //战斗
                name: "bags",
                component: () =>
                    import ( /* webpackChunkName: "combat" */ '@/page/home/bag/index'),
            },
        ]

    },
    {
        path: '/combat', //战斗
        name: "combat",
        component: () =>
            import ( /* webpackChunkName: "combat" */ '@/page/home/combat'),
    },
    {
        path: '/beginCombat', //开始战斗
        name: "beginCombat",
        component: () =>
            import ( /* webpackChunkName: "combat" */ '@/page/home/combat/beginCombat'),
    },
    {
        path: '/sprinteDetail', //精灵详情
        name: "sprinteDetail",
        component: () =>
            import ( /* webpackChunkName: "maisprinteDetailn" */ '@/page/home/sprinte/sprinteDetail'),
    },
    {
        path: '/sprinteDetailSkill', //精灵技能
        name: "sprinteDetailSkill",
        component: () =>
            import ( /* webpackChunkName: "sprintDetailSkill" */ '@/page/home/sprinte/sprinteDetailSkill'),
    },
    {
        path: '/sprinteDetailEvolve', //精灵技能
        name: "sprinteDetailEvolve",
        component: () =>
            import ( /* webpackChunkName: "sprintDetailSkill" */ '@/page/home/sprinte/sprinteDetailEvolve'),
    },

]

export default routers


