import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import newStepRouter from './modules/guide'
import mainRouter from './modules/main'
Vue.use(Router)
console.log(mainRouter)
const router = new Router({
    routes: [{
            path: '/',
            redirect: '/login'
        },
        {
            path: '/login',
            name: 'login',
            component: () =>
                import ( /*webpackChunkName:"login"*/ '@/page/auth/login')
        },
        ...newStepRouter,
        ...mainRouter
    ]
})

console.log(router)
    // const authRouterMap = [{
    //     patt: '',
    //     name: ''
    // }, ]

export default router;


